
'use client';

import { useState, useEffect, useTransition, useCallback } from 'react';
import { z } from 'zod';
import { useForm } from 'react-hook-form';
import { zodResolver } from '@hookform/resolvers/zod';
import { database, ref, get } from '@/lib/firebase';
import { generateAlertNotification } from '@/ai/flows/generate-alert-notification';
import { sendNotification } from '@/ai/flows/send-notification';

import { Card, CardContent, CardDescription, CardHeader, CardTitle, CardFooter } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { RadioGroup, RadioGroupItem } from '@/components/ui/radio-group';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { Skeleton } from '@/components/ui/skeleton';
import { useToast } from '@/hooks/use-toast';
import { Loader2, Send, Mail, MessageSquare } from 'lucide-react';
import { Form, FormControl, FormField, FormItem, FormLabel, FormMessage } from '@/components/ui/form';

interface FirebaseDevice {
    key: string;
    Name: string;
    Email: string;
    "Contact Number": string;
}

const formSchema = z.object({
  alertType: z.enum(['Tampering', 'Outage-Scheduled', 'Outage-Unscheduled-Maintenance', 'Outage-Unscheduled-Tampering'], { required_error: "Alert type is required." }),
  recipient: z.string().min(1, "Recipient is required."),
  notificationMethod: z.enum(['SMS', 'GMail']),
});

type FormValues = z.infer<typeof formSchema>;

export function AlertsForm({ deviceId }: { deviceId: string }) {
  const { toast } = useToast();
  const [isPending, startTransition] = useTransition();
  const [isGenerating, setIsGenerating] = useState(false);
  const [deviceData, setDeviceData] = useState<FirebaseDevice | null>(null);
  const [loadingDevice, setLoadingDevice] = useState(true);
  
  const [generatedMessage, setGeneratedMessage] = useState<{ subject: string; body: string } | null>(null);

  const form = useForm<FormValues>({
    resolver: zodResolver(formSchema),
    defaultValues: {
      alertType: 'Tampering',
      notificationMethod: 'GMail',
      recipient: '',
    },
  });
  
  const notificationMethod = form.watch('notificationMethod');
  const alertType = form.watch('alertType');

  const handleGenerateAlert = useCallback(async (alertType: FormValues['alertType'], notificationMethod: FormValues['notificationMethod'], currentDeviceData: FirebaseDevice) => {
    if (!currentDeviceData) return;
    
    setIsGenerating(true);
    setGeneratedMessage(null);
    try {
      const result = await generateAlertNotification({
        customerName: currentDeviceData.Name,
        meterId: currentDeviceData.key,
        alertType: alertType,
        notificationMethod: notificationMethod,
      });
      setGeneratedMessage(result);
    } catch (error) {
      console.error(error);
      toast({ variant: 'destructive', title: 'Error', description: 'Failed to generate alert notification.' });
    } finally {
      setIsGenerating(false);
    }
  }, [toast]);

  useEffect(() => {
    const fetchDeviceData = async () => {
      setLoadingDevice(true);
      try {
        const deviceRef = ref(database, `devices/${deviceId}`);
        const snapshot = await get(deviceRef);
        if (snapshot.exists()) {
          const data = snapshot.val();
          const newDeviceData: FirebaseDevice = { ...data, key: deviceId };
          setDeviceData(newDeviceData);
          
          const initialNotificationMethod = form.getValues('notificationMethod');
          const initialAlertType = form.getValues('alertType');
          form.setValue('recipient', initialNotificationMethod === 'GMail' ? newDeviceData.Email : newDeviceData['Contact Number']);
          
          await handleGenerateAlert(initialAlertType, initialNotificationMethod, newDeviceData);

        } else {
          toast({ variant: 'destructive', title: 'Error', description: 'Device not found.' });
        }
      } catch (error) {
        console.error(error);
        toast({ variant: 'destructive', title: 'Error', description: 'Failed to fetch device data.' });
      } finally {
        setLoadingDevice(false);
      }
    };
    fetchDeviceData();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [deviceId, toast]);

  useEffect(() => {
    if (deviceData) {
        form.setValue('recipient', notificationMethod === 'GMail' ? deviceData.Email : deviceData['Contact Number']);
        handleGenerateAlert(alertType, notificationMethod, deviceData);
    }
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [notificationMethod, alertType]);


  const onSubmit = (values: FormValues) => {
    if (!generatedMessage) {
        toast({ variant: 'destructive', title: 'Error', description: 'Please generate a message first.' });
        return;
    }

    startTransition(async () => {
        try {
            const result = await sendNotification({
                recipient: values.recipient,
                subject: generatedMessage.subject,
                body: generatedMessage.body,
                method: values.notificationMethod,
            });

            if (result.success) {
                toast({ title: 'Success', description: result.message });
                setGeneratedMessage(null);
            } else {
                toast({ variant: 'destructive', title: 'Error', description: result.message });
            }
        } catch (error) {
            console.error(error);
            toast({ variant: 'destructive', title: 'Error', description: 'An unexpected error occurred.' });
        }
    });
  };

  if (loadingDevice && !deviceData) {
    return (
      <Card>
        <CardHeader>
          <Skeleton className="h-8 w-3/4" />
          <Skeleton className="h-4 w-1/2" />
        </CardHeader>
        <CardContent>
          <Skeleton className="h-40 w-full" />
        </CardContent>
      </Card>
    );
  }

  return (
    <Form {...form}>
      <form onSubmit={form.handleSubmit(onSubmit)} className="space-y-6">
        <div className="grid grid-cols-1 gap-8 md:grid-cols-3">
          <div className="md:col-span-1">
             <Card>
                <CardHeader>
                    <CardTitle>1. Configure Alert</CardTitle>
                    <CardDescription>
                        Select the type of alert to send to the customer. The message will regenerate automatically.
                    </CardDescription>
                </CardHeader>
                <CardContent className="space-y-4">
                    <FormField
                      control={form.control}
                      name="alertType"
                      render={({ field }) => (
                        <FormItem>
                          <FormLabel>Alert Type</FormLabel>
                          <Select onValueChange={field.onChange} defaultValue={field.value} disabled={isGenerating || isPending}>
                            <FormControl>
                              <SelectTrigger>
                                <SelectValue placeholder="Select an alert type" />
                              </SelectTrigger>
                            </FormControl>
                            <SelectContent>
                              <SelectItem value="Tampering">Tampering</SelectItem>
                              <SelectItem value="Outage-Scheduled">Outage (Scheduled)</SelectItem>
                              <SelectItem value="Outage-Unscheduled-Maintenance">Outage (Unscheduled Maintenance)</SelectItem>
                              <SelectItem value="Outage-Unscheduled-Tampering">Outage (Unscheduled Tampering)</SelectItem>
                            </SelectContent>
                          </Select>
                          <FormMessage />
                        </FormItem>
                      )}
                    />
                </CardContent>
            </Card>
          </div>
          
          <div className="md:col-span-2">
            <Card>
                <CardHeader>
                    <CardTitle>2. Review and Send Alert</CardTitle>
                    <CardDescription>
                        Review the generated alert below, then select the delivery method and send it.
                    </CardDescription>
                </CardHeader>
                <CardContent className="space-y-4">
                    {(isGenerating || (loadingDevice && !generatedMessage)) && (
                        <div className="space-y-2">
                           <Skeleton className="h-8 w-1/3" />
                           <Skeleton className="h-24 w-full" />
                        </div>
                    )}

                    {generatedMessage && !isGenerating && (
                        <div className="space-y-4 rounded-md border border-destructive/50 bg-destructive/10 p-4">
                            <div className="space-y-1">
                                <Label className="text-destructive">Subject</Label>
                                <p className="text-sm font-medium">{generatedMessage.subject}</p>
                            </div>
                             <div className="space-y-1">
                                <Label className="text-destructive">Body</Label>
                                <p className="text-sm whitespace-pre-wrap">{generatedMessage.body}</p>
                            </div>
                        </div>
                    )}
                    
                    <FormField
                      control={form.control}
                      name="notificationMethod"
                      render={({ field }) => (
                        <FormItem className="space-y-3">
                          <FormLabel>Notification Method</FormLabel>
                          <FormControl>
                            <RadioGroup
                              onValueChange={field.onChange}
                              defaultValue={field.value}
                              className="flex items-center gap-4"
                              disabled={isPending || isGenerating}
                            >
                              <FormItem className="flex items-center space-x-2 space-y-0">
                                <FormControl>
                                  <RadioGroupItem value="GMail" id="gmail"/>
                                </FormControl>
                                <FormLabel htmlFor="gmail" className="font-normal flex items-center gap-2"><Mail/> GMail</FormLabel>
                              </FormItem>
                              <FormItem className="flex items-center space-x-2 space-y-0">
                                <FormControl>
                                  <RadioGroupItem value="SMS" id="sms"/>
                                </FormControl>
                                <FormLabel htmlFor="sms" className="font-normal flex items-center gap-2"><MessageSquare/> SMS</FormLabel>
                              </FormItem>
                            </RadioGroup>
                          </FormControl>
                          <FormMessage />
                        </FormItem>
                      )}
                    />

                    <FormField
                        control={form.control}
                        name="recipient"
                        render={({ field }) => (
                            <FormItem>
                            <FormLabel>Recipient</FormLabel>
                            <FormControl>
                                <Input placeholder="Email address or phone number" {...field} disabled={isPending || isGenerating}/>
                            </FormControl>
                            <FormMessage />
                            </FormItem>
                        )}
                    />
                </CardContent>
                 <CardFooter>
                    <Button type="submit" className="w-full" disabled={!generatedMessage || isPending || isGenerating} variant={generatedMessage ? 'destructive' : 'default'}>
                        {isPending ? (
                             <><Loader2 className="mr-2 h-4 w-4 animate-spin" /> Sending Alert...</>
                        ) : (
                             <><Send className="mr-2 h-4 w-4" /> Send Alert</>
                        )}
                    </Button>
                </CardFooter>
            </Card>
          </div>
        </div>
      </form>
    </Form>
  );
}
